@font-face {
  font-family: "Dovemayo_gothic";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2302@1.1/Dovemayo_gothic.woff2")
    format("woff2");
  font-weight: normal;
  font-style: normal;
}
.intro {
  text-align: center;
  max-width: 45rem;
  width: 100%;
  margin: auto;
  margin-top: -8rem;
  position: relative;
  background-color: rgba(32, 32, 32, 0.95);
  color: lightgray;
  border-radius: 14px;
  padding: 2rem;
}

.intro h2 {
  font-size: 1.8rem;
  margin: 5px;
}

.intro span {
  position: relative; /* spanÌÉúÍ∑∏Î•º ÏÉÅÎåÄÏúÑÏπòÎ°ú ÏÑ§Ï†ï */
  display: inline-block; /* spanÌÉúÍ∑∏Î•º Ïù∏ÎùºÏù∏ Î∏îÎ°ùÏúºÎ°ú ÏÑ§Ï†ï */
  margin-bottom: 30px;
  font-family: "Dovemayo_gothic";
  font-size: larger;
  font-weight: lighter;
  background-color: lightgray;
  border-radius: 10px;
  color: black;
  padding: 3px;
  box-sizing: border-box;
  transition: all 0.6s; /* Î≥ÄÍ≤ΩÎêú ÏÜçÏÑ±Ïóê ÎåÄÌï¥ 0.6Ï¥àÍ∞Ñ Ìä∏ÎûúÏßÄÏÖò Ï†ÅÏö© */
}

.intro span:before {
  content: ""; /* Í∞ÄÏÉÅ ÏöîÏÜåÎ°ú ÏÉùÏÑ±Îêú ÏΩòÌÖêÏ∏†Í∞Ä ÏóÜÎèÑÎ°ù ÏÑ§Ï†ï */
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.167); /* Ìà¨Î™ÖÎèÑÎ•º Í∞ÄÏßÑ Í≤ÄÏùÄÏÉâ Î∞∞Í≤Ω ÏÑ§Ï†ï */
  border-radius: 10px;
  transition: all 0.6s;
}

.intro span:hover:before {
  width: 100%; /* ÎßàÏö∞Ïä§ Ïò§Î≤Ñ Ïãú Ï†ÑÏ≤¥ Í∏∏Ïù¥Î°ú ÏÑ§Ï†ï */
}

.intro span:hover {
  background-color: rgb(28, 25, 25);
  color: azure;
  padding: 4.5px;
  cursor: pointer;
}
